<template>
  <section>
    <swiper class="swiper py-2 px-8" :options="swiperOption">
      <swiper-slide
        v-for="(video, index) in videos.feed.entry"
        :key="index"
        class="rounded-lg"
      >
      <a :href="video['link'][0]['$'].href" target="_blank">
        <img
          class="rounded-lg ring-2 ring-gray-600"
          :src="video['media:group'][0]['media:thumbnail'][0]['$'].url"
          :alt="video['media:group'][0]['media:title']"
          :title="video['media:group'][0]['media:title']"
        />
        </a>
      </swiper-slide>
    </swiper>
  </section>
</template>
<script>
import axios from "axios";
var parseString = require("xml2js").parseString;

import { Swiper, SwiperSlide, directive } from "vue-awesome-swiper";
import "swiper/swiper.min.css";

export default {
  name: "VideoFeed",
  data() {
    return {
      videos: [],
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 30,
        freeMode: true,

        breakpoints: {
          1024: {
            slidesPerView: 4,
            spaceBetween: 40,
          },
          768: {
            slidesPerView: 3,
            spaceBetween: 30,
          },
          640: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          320: {
            slidesPerView: 1,
            spaceBetween: 10,
          },
        },
      },
    };
  },
  mounted() {
    axios.get("https://feed-sydeerdj-com.vercel.app/").then((response) => {
      parseString(response.data, (err, result) => {
        if (err) {
          //Do something
        } else {
          this.videos = result;
        }
      });
    });
  },
  computed: {},
  watch: {},
  methods: {},
  components: {
    Swiper,
    SwiperSlide,
  },
  directives: {
    swiper: directive,
  },
};
</script>